    },
  // 질문 클릭시, 활성화된 인덱스 토글 메서드 
  methods: {
    toggle(index) {
      // 액티브인덱스가 클릭된 인덱스와 같으면 닫고, 다르면 열기
      this.activeIndex = this.activeIndex === index ? null : index;
    },
  },
  };
